li {
  padding: 10px 0;
}

footer {
  position: fixed;
  height: 100px;
  bottom: 0;
  width: 100%;
}

/* Class */
.navBar {
  width: 100%;
  background-color: #111;
  color: white;
  padding: 10px;
}

.links {
height: 0;
overflow: hidden;
list-style-type: none;
font-weight: bold;
}

.show-nav {
  height: 120px;
}
.mb-3 {
  background-color: #111;
}
/* Media screen size */
@media screen and (min-width: 768px) {
  button {
      display: none;
  }

  .links {
      height: auto;
      display: flex;
  }

  li {
      padding: 0 15px;
  }
}

